import React, { Component } from 'react';
//import Skills from '../Components/Skills';

class Resume extends Component {
  render() {

    if(this.props.data){
      var skillmessage = this.props.data.skillmessage;
      var education = this.props.data.education.map((education) => {
        return <div key={education.school}><h3>{education.school} <a href={education.link} target="_blank" rel="noopener noreferrer" className="fa fa-arrow-circle-right"><i></i></a></h3>
        <p className="info">{education.degree}<span>&bull;</span><em className="date">{education.graduated}</em></p>
        <p>{education.description}</p></div>
      })
      var work = this.props.data.work.map((work) => {
        return <div key={work.company}><h3>{work.company} <a href={work.link} target="_blank" rel="noopener noreferrer" className="fa fa-arrow-circle-right"><i></i></a></h3>
            <p className="info">{work.title}<span>&bull;</span> <em className="date">{work.years}</em></p>
            <p>{work.description}</p>
            {/* Used for research internship only */}
            <p>
              <h6>{work.para}<a href={work.report_link} target="_blank" rel="noopener noreferrer" className={work.icon}><i></i></a></h6>
            </p>
        </div>
      })
      var publication = this.props.data.publication.map((publication) => {
        return <div key={publication.publication_title}><h3>{publication.publication_title}</h3>
              <p className='info'>{publication.publication_type}<span>&bull;</span><em className="date">{publication.date_of_publication}</em></p>
              <p className='info'>{publication.authors}</p>
              <p>{publication.description}</p>
              <p>
              <h6>Paper Link: <a href={publication.link}className="fa fa-link"><i> will soon be updated</i></a></h6>
              </p>
        </div>
      })
      // var skills = this.props.data.skills.map((skills) => {
      //   var className = 'bar-expand '+skills.name.toLowerCase();
      //   return <li key={skills.name}><span style={{width:skills.level}}className={className}></span><em>{skills.name}</em></li>
      // })
      var skills = this.props.data.skills.map(function(skills){
        var projectImage = 'images/tech/'+skills.image;
          return <div key={skills.name} className="columns feature-item">
                    <a href={skills.link}><img className='skill' alt={skills.name} src={projectImage} /></a>
                    <h5>{skills.name}</h5>
                    <p>{skills.description}</p>
                 </div>
        })
    }

    return (
      <section id="resume">

      <div className="row education">
         <div className="three columns header-col">
            <h1><span>Education</span></h1>
         </div>

         <div className="nine columns main-col">
            <div className="row item">
               <div className="twelve columns">
                 {education}
               </div>
            </div>
         </div>
      </div>


      <div className="row work">

         <div className="three columns header-col">
            <h1><span>Experience</span></h1>
         </div>

         <div className="nine columns main-col">
          {work}
        </div>
    </div>

      <div className="row publication">

        <div className="three columns header-col">
          <h1><span>Publication</span></h1>
        </div>

        <div className="nine columns main-col">
          {publication}
        </div>
      </div>



      {/* <div className="row skill">

         <div className="three columns header-col">
            <h1><span>Skills</span></h1>
         </div>

         <div className="nine columns main-col">

            <p>{skillmessage}
            </p>

				<div className="bars">
				   <ul className="skills">
					  {skills}
					</ul>
        </div>
        
			</div>
      </div> */}

      <div className="row skill">

         <div className="three columns header-col">
            <h1><span>Skills</span></h1>
         </div>

         <div>
           <div className="nine columns main-col"><p className="lead center">{skillmessage}</p></div>
				   <ul className="bgrid-quarters s-bgrid-thirds cf">
					  {skills}
					 </ul>
			  </div>
      </div>

   </section>
    );
  }
}

export default Resume;
